.screen {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 10vh;
  position: sticky;
  top: 0;
  background-color: white;
  z-index: 999;
}

.nav-label {
  color: rgb(143, 143, 143);
  margin: 25px 10px 15px;
  transition: transform 0.2s, border-bottom 0.5s;
  font-size: 14px;
}

.nav-label:hover {
  transform: scale(1.08);
  border-bottom: 1px solid rgb(255, 209, 140);
}

.row {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  width: 800px;
}

.nav-col {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0px 25px 0px;
}
